.demand-forecast {
    height: 100%;
    background-color: #0D1016;
    padding: 0 0 14px 0;
}

.demand-forecast_header {
    padding: 0 17px;
    height: 64px;
    background: #212029;
    border-radius: 10px;
    backdrop-filter: blur(25px);
    display: flex;
    align-items: center;
}

.demand-forecast_header_btn {
    width: 65px;
    height: 32px;
    color: #fff;
    background: #0F9D90 !important;
    border-radius: 3px;
    border: none;
    margin-left: 4px;
}
.demand-forecast_header_btn:focus, .demand-forecast_header_btn:hover{
    color: #fff;
    border-color: #0F9D90;
}


.demand-forecast_content {
    margin-top: 16px;
    height: calc(100% - 80px);
    overflow: scroll;
    display: flex;
    justify-content: space-between;
}

.demand-forecast_content::-webkit-scrollbar {
    display: none;
}


.demand-forecast_content_left {
    width: 49.5%;
    height: auto;
    flex-shrink: 0;
    margin-right: 16px;
    background: #212029;
    border-radius: 10px;
    backdrop-filter: blur(25px);
    overflow-y: scroll;
}

.demand-forecast_content_left::-webkit-scrollbar {
    display: none;
}


.demand-forecast_content_left.is-rendered{
    height: fit-content;
}
.left-query-btn{
    color: #fff;
    background: #0F9D90;
    border-radius: 3px;
    border: none;
}
.left-query-btn:focus, .left-query-btn:hover{
    color: #fff;
    border-color: #0F9D90 !important;
    background: #0F9D90;
}

.demand-forecast_content_left_header {
    height: auto;
    padding: 16px;
}

.demand-forecast_content_right {
    width: 49.5%;
    flex-shrink: 1;
    height: auto;
    height: auto;
    background: #212029;
    border-radius: 10px;
    backdrop-filter: blur(25px);
    overflow-y: scroll;
}
.demand-forecast_content_right::-webkit-scrollbar{
    display: none;
}

.demand-forecast_content_right_header {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px;

}

.title-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}

.demand-forecast-dots {
    display: inline-block;
    width: 6px;
    height: 24px;
    background: #0F9D90;
    border-radius: 5px
}

.demand-forecast-title {
    font-family: PingFangSC, PingFang SC;
    font-weight: 500;
    font-size: 16px;
    color: #FFFFFF;
    font-style: normal;
    margin-left: 8px;
}

.expand-icon {
    display: inline-block;
    width: 12px;
    height: 12px;
    background-image: url(./assets/expand-icon.png);
    background-size: contain;
    cursor: pointer;
}

.demand-forecast-right-top {
    margin-top: 8px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.right-img {
    width: 60px;
    height: 45px;
    background-image: url(./assets/right-img.png);
    background-size: contain;
}

.right-text {
    margin-top: 13px;
    font-family: PingFangSC, PingFang SC;
    font-weight: 400;
    font-size: 14px;
    color: #FFFFFF;
}

.demand-forecast-right-center {
    margin: 0 auto;
    width: 550px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}


.special-right-text-wrap{
    right: -1%;
    top: 2%;
}

.left-text,
.other-right-text {
    font-family: PingFangSC, PingFang SC;
    font-weight: 400;
    font-size: 12px;
    color: #FFFFFF;
    margin-bottom: 5px;
}

.left-text:last-child{
    margin-bottom: 0;
}

.other-right-text:last-child{
    margin-bottom: 0;
}

.center-img-top {
    width: 60px;
    height: 32px;
    margin-top: 30px;
    background-image: url(./assets/arrow.gif);
    background-repeat: no-repeat;
    background-size: cover;
    transform: rotate(88deg);
}
.center-img-top.overturn{
    transform: rotate(270deg);
}
.center-img-top.completed{
    background: #0F9D90;
    height: 2px;
    transform: rotate(270deg);
    margin-top: 37px;
}

.model-wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 34px;
}

.modal-img {
    width: 52px;
    height: 61px;
    background-image: url(./assets/modal-img.png);
    background-size: contain;
}

.model-text {
    font-family: PingFangSC, PingFang SC;
    font-weight: 400;
    font-size: 14px;
    color: #FFFFFF;
}

.demand-forecast-right-bottom-0 {
    height: 80px;
    margin-top: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.demand-forecast-right-bottom-1{
    height: 80px;
    margin-top: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    .bottom-gif-1{
        width:120px;
        height: 60px;
        background-image: url(./assets/arrow.gif);
        background-repeat: no-repeat;
        background-size: cover;
        transform: rotate(90deg) scale(0.6);
        position: relative;
        left: 0;
    }
    .bottom-gif-1.overturn{
        transform: rotate(270deg) scale(0.6);
    }
    .bottom-gif-1.completed{
        background: #0F9D90;
        height: 2px;
        transform: rotate(90deg);
    }
}

.demand-forecast-right-bottom-2{
    height: 80px;
    margin-top: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    .bottom-gif-1{
        width:120px;
        height: 60px;
        background-image: url(./assets/arrow.gif);
        background-repeat: no-repeat;
        background-size: cover;
        transform: rotate(132deg) scale(0.6);
        position: relative;
        left: 0;
    }
    .bottom-gif-1.overturn{
        transform: rotate(312deg) scale(0.6);
    }
    .bottom-gif-1.completed{
        background: #0F9D90;
        height: 2px;
        transform: rotate(132deg);
        top: -3px;
    }
    .bottom-gif-2{
        width:120px;
        height: 60px;
        background-image: url(./assets/arrow.gif);
        background-repeat: no-repeat;
        background-size: cover;
        transform: rotate(49deg) scale(0.6);
        position: relative;
        left: 0;
        top: -5px;
    }
    .bottom-gif-2.overturn{
        transform: rotate(229deg) scale(0.6);
        top: 0;
    }
    .bottom-gif-2.completed{
        background: #0F9D90;
        height: 2px;
        transform: rotate(49deg);
    }
}


.demand-forecast-right-bottom-3,
.demand-forecast-right-bottom-4,
.demand-forecast-right-bottom-5,
.demand-forecast-right-bottom-6,
.demand-forecast-right-bottom-7,
.demand-forecast-right-bottom-8,
.demand-forecast-right-bottom-9,
.demand-forecast-right-bottom-10{
    height: 80px;
    margin-top: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    .bottom-gif-1{
        width:120px;
        height: 60px;
        background-image: url(./assets/arrow.gif);
        background-repeat: no-repeat;
        background-size: cover;
        transform: rotate(125deg) scale(0.6);
        position: relative;
        left: 0;
        top: 0;
    }
    .bottom-gif-1.overturn{
        transform: rotate(305deg) scale(0.6);
    }
    .bottom-gif-1.completed{
        background: #0F9D90;
        height: 2px;
        transform: rotate(132deg);
    }
    .bottom-gif-2{
        width:106px;
        height: 60px;
        background-image: url(./assets/arrow.gif);
        background-repeat: no-repeat;
        background-size: cover;
        transform: rotate(90deg) scale(0.6);
        position: relative;
        left: 0;
        top: -5px;
    }
    .bottom-gif-2.overturn{
        transform: rotate(270deg) scale(0.6);
    }
    .bottom-gif-2.completed{
        background: #0F9D90;
        height: 2px;
        transform: rotate(90deg);
    }
    .bottom-gif-3{
        width:120px;
        height: 60px;
        background-image: url(./assets/arrow.gif);
        background-repeat: no-repeat;
        background-size: cover;
        transform: rotate(49deg) scale(0.6);
        position: relative;
        left: 0;
        top: -5px;

    }
    .bottom-gif-3.overturn{
        transform: rotate(229deg) scale(0.6);
    }
    .bottom-gif-3.completed{
        background: #0F9D90;
        height: 2px;
        transform:rotate(49deg) scale(1);
    }
}



.arrow-bottom {
    width: 406px;
    height: 97px;
    background-image: url(./assets/arrow-up.png);
    background-size: contain;
}

.dots-1 {
    width: auto;
    height: auto;
    background: #312D36;
    border-radius: 8px;
    backdrop-filter: blur(18px);
