    align-items: center;
    border-radius: 8px 8px 0 0;
}

.round {
    width: 10px;
    height: 10px;
    background: #D8D8D8;
    border-radius: 10px;
    flex-shrink: 0;
}
.round.online-status{
    background: #0F9D90;
}

.dots-1-header-text {
    font-family: PingFangSC, PingFang SC;
    font-weight: 400;
    font-size: 12px;
    color: #FFFFFF;
    margin-left: 9px;
    word-wrap: break-word;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.dots1-content {
    padding: 9px 12px 11px 12px;
}

.dots1-text {
    font-family: PingFangSC, PingFang SC;
    font-weight: 400;
    font-size: 10px;
    color: #D1D2D6;
    line-height: 12px;
}


.strategy-title-wrap {
    display: flex;
    justify-content: space-between;
    padding: 0 17px;
}

.strategy-title {
